.container-spectate {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    gap: 1em 1em;
    grid-auto-flow: row;
    grid-template-areas: "Spacer Spacer Spacer Sword Spacer2 Spacer2 Spacer2" "Attack-Tanks Attack-Tanks Attack-Tanks Attack-Tanks Attack-Tanks Attack-Tanks Attack-Tanks" "Defense-Tanks Defense-Tanks Defense-Tanks Defense-Tanks Defense-Tanks Defense-Tanks Defense-Tanks" "Spacer3 Spacer3 Spacer3 Shield Spacer4 Spacer4 Spacer4";
    height: 98vh;
    padding-bottom: 2vh;
    width: 94vw;
    padding-right: 3vw;
    padding-left: 3vw;
}

.Spacer {
    grid-area: Spacer;
}

.Spacer2 {
    grid-area: Spacer2;
}
.Spacer3 {
    grid-area: Spacer3;
}
.Spacer4 {
    grid-area: Spacer4;
}