.img {
  max-width: 100%;
  object-fit: contain;
  align-items: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.sideLogo {
  width: 50%;
  margin: 0;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.logo {
  height: 25vh;
  width: 100/7vw;
  align-content: center;
}

.tankImg {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.imageOne {
  z-index: 0;
}

.imageTwo {
  z-index: 1;
}

.label {
  position: relative;
  z-index: 2;
  color: white;
  text-align: center;
  bottom: -2.5vh;
}
.container {
  position: relative;
  height: 25vh;
  width: 100/7vw;
}